/* Footer Section Styles */
.footer {
    padding: 2rem 1rem;
    background: var(--primary-dark); /* Use #2B221C */
    color: var(--light-gold); /* Use #D6B58C */
    text-align: center;
    border-top: 1px solid var(--light-brown); /* Use #453025 */
  }
  
  .footer p {
    font-size: 1rem;
    margin: 0;
    opacity: 0.9;
  }
  
  .footer a {
    color: var(--accent-gold); /* Use #E6B07C */
    text-decoration: none;
    transition: color 0.3s ease;
  }
  
  .footer a:hover {
    color: var(--light-gold); /* Use #D6B58C */
  }
  
  /* Responsive Design */
  @media (max-width: 768px) {
    .footer {
      padding: 1.5rem 1rem;
    }
  
    .footer p {
      font-size: 0.875rem;
    }
  }