:root {
  --primary-dark: #2b221c;
  --secondary-dark: #3a211a;
  --accent-gold: #e6b07c;
  --light-gold: #d6b58c;
  --dark-brown: #714837;
  --light-brown: #453025;
  --text-light: #ffffff;
  --text-dark: #2b221c;
}
/* Media Queries for Responsiveness */
@media (max-width: 1024px) {
  .service-card {
    flex: 1 1 calc(50% - 1rem); /* Two cards per row on medium screens */
  }
}

@media (max-width: 768px) {
  .service-card {
    flex: 1 1 100%; /* One card per row on small screens */
  }
}
